#header {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 20px;
}

#header__links {
  list-style-type: none;
}

.header__link {
  display: inline-block;
  margin: 0 20px;
  border-radius: 10px;
  padding: 20px;
}

@media screen and (max-width: 500px) {
  #header {
    
  }

  #header__links {

  }

  .header__link {
    display: block;
  }
}
