#alert {
  position: fixed;
  top: -100px;
  left: 50%;
  margin-left: -150px;
  width: 300px;
  text-align: center;
  background-color: rgb(13, 91, 236);
  padding: 30px;
  animation-timing-function: linear;
  animation-duration: .3s;
  animation-fill-mode: forwards;
}

#alert__close-btn {
  background-color: rgb(52, 231, 16);
  padding: 5px 10px;
  cursor: pointer;
  margin-top: 20px;
}

.alert-visible {
  visibility: visible;
}

.alert-hidden {
  visibility: hidden
}

.alert-animate {
  animation-name: drop;
}

@keyframes drop {
  from {
    top: -100px;
  }
  to {
    top: 100px;
  }
}
