.products {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.products__product {
  position: relative;
  width: 200px;
  height: 200px;
  border: 1px solid rgb(0, 0, 0);
  margin: 20px;
  padding: 10px;
  transition-property: box-shadow;
  transition-timing-function: linear;
  transition-duration: .3s;
}

.product__buy-btn {
  cursor: pointer;
  padding: 10px;
  position: absolute;
  right: 10px;
  bottom: 10px;
  background-color: rgb(94, 95, 163);
  color: white;
}

.products__product:hover {
  box-shadow: 5px 5px 10px black;
}
